import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet-async';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

//firestore
import { useFirebaseAuth } from 'src/contexts/FirebaseAuthProvider';
import { collection, getDocs, doc, updateDoc } from "firebase/firestore"; 
import { OrderInterface, convertOrderQuerySnapshots } from 'src/models/orders';

//components
import SnackbarApp, { SnackbarProps } from 'src/components/Snackbar';
import OrderTable from './OrderTable';

function OrderList() {
    const { database } = useFirebaseAuth();
    const [pushUpdate, setPushUpdate] = useState<boolean>(false);
    const [butilOrders, setButilOrders] = useState<OrderInterface[]>([]);
    const [snackbarApp, setSnackBarApp] = useState<SnackbarProps>({
        openSnackbar: false,
        success: true,
        message: ''
    });

    useEffect(() => {
        async function getOrders() {
            let orderList: OrderInterface[] = [];
            const querySnapshot = await getDocs(collection(database, "orders"));

            querySnapshot.forEach((doc) => {
                const currentOrder = convertOrderQuerySnapshots(doc);
                orderList.push(currentOrder.getOrder());
            });

            setButilOrders(state => orderList);
        }

        getOrders();
    }, [database, pushUpdate]);

    function modifyDelivered(orderId: string, status: boolean) {
        const orderRef = doc(database, "orders", orderId);
        updateDoc(orderRef, { delivered: !status }).then(response => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: true, message: 'Updated the order successfully!' });
        }).catch(error => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: false, message: 'Failed to update order.' });
        });
        setPushUpdate(!pushUpdate);
    }

    function modifyPaid(orderId: string, status: boolean) {
        const orderRef = doc(database, "orders", orderId);
        updateDoc(orderRef, { paid: !status }).then(response => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: true, message: 'Updated the order successfully!' });
        }).catch(error => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: false, message: 'Failed to update order.' });
        });
        setPushUpdate(!pushUpdate);
    }

    return (
        <>
            <Helmet>
                <title>Orders</title>
            </Helmet>
            <Container>
                <Box
                    sx={{
                        display: 'flex',
                        justifyContent: 'start',
                        alignItems: 'center',
                        marginTop: 5,
                        marginBottom: 3
                    }}
                >
                    <Typography variant="h1" color="primary">Orders</Typography>
                </Box>
                <OrderTable 
                    orders={butilOrders} 
                    modifyDelivered={(orderId, status) => modifyDelivered(orderId, status)}
                    modifyPaid={(orderId, status) => modifyPaid(orderId, status)}
                />
            </Container>
            <SnackbarApp openSnackbar={snackbarApp.openSnackbar} success={snackbarApp.success} message={snackbarApp.message} setOpenSnackbar={value => setSnackBarApp({ ...snackbarApp, openSnackbar: value })} />
        </>
    )
}

export default OrderList
