import React from 'react';
import {
    Tooltip,
    IconButton,
    TableCell,
    TableRow,
    Typography,
    Collapse,
    Table,
    TableHead,
    TableBody,
    Box,
    useTheme,
  } from '@mui/material';
import Label from 'src/components/Label';
import AllInboxTwoToneIcon from '@mui/icons-material/AllInboxTwoTone';
import CreditCardTwoToneIcon from '@mui/icons-material/CreditCardTwoTone';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import DeliveryDiningTwoToneIcon from '@mui/icons-material/DeliveryDiningTwoTone';
import NotListedLocationTwoToneIcon from '@mui/icons-material/NotListedLocationTwoTone';
import CreditCardOffTwoToneIcon from '@mui/icons-material/CreditCardOffTwoTone';
import { OrderInterface } from 'src/models/orders';
import { useNavigate } from 'react-router-dom';

type OrderStatus = "completed" | "pending" | "unassigned" | "paid" | "delivered";

const getStatusLabel = (orderStatus: OrderStatus): JSX.Element => {
    switch (orderStatus) {
        case 'completed': 
            return <Label color='success'>Completed</Label>;
        case 'paid': 
            return <Label color='info'>Paid</Label>;
        case 'delivered': 
            return <Label color='info'>Delivered</Label>;
        case 'pending': 
            return <Label color='warning'>Pending</Label>;
        default: 
            return <Label color='error'>Unassigned</Label>;
           
    }
  
    
};

interface OrderProps {
    order: OrderInterface,
    modifyDelivered: (orderId: string, status: boolean) => void,
    modifyPaid: (orderId: string, status: boolean) => void
}

function OrderRow({ order, modifyDelivered, modifyPaid }: OrderProps) {
    const navigate = useNavigate();
    const theme = useTheme();
    const [open, setOpen] = React.useState<boolean>(false);

    return (
        <>
            <TableRow
                hover
                //key={user.userId}
                //selected={isUserSelected}
            >
                <TableCell>
                    <IconButton
                        aria-label="expand row"
                        size="small"
                        onClick={() => setOpen(!open)}
                    >
                        {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
                    </IconButton>
                </TableCell>
                <TableCell>
                    <Typography
                        variant="body1"
                        fontWeight="bold"
                        color="text.primary"
                        gutterBottom
                        noWrap
                    >
                        {order.orderId}
                    </Typography>
                </TableCell>
                <TableCell>
                    <Typography
                        variant="body1"
                        fontWeight="bold"
                        color="text.primary"
                        gutterBottom
                        noWrap
                    >
                        {order.customer.userName}
                    </Typography>
                </TableCell>
                <TableCell>
                    <Typography
                        variant="body1"
                        fontWeight="bold"
                        color="text.primary"
                        gutterBottom
                        noWrap
                    >
                        {new Date(order.orderDate).toLocaleDateString(undefined, { weekday: 'short', year: 'numeric', month: 'short', day: 'numeric' })}
                    </Typography>
                </TableCell>
                <TableCell>
                    <Typography
                        variant="body1"
                        fontWeight="bold"
                        color="text.primary"
                        gutterBottom
                        noWrap
                    >
                        {'₱' + order.amountDue}
                    </Typography>
                </TableCell>
                <TableCell>
                    <Typography
                        variant="body1"
                        fontWeight="bold"
                        color="text.primary"
                        gutterBottom
                        noWrap
                    >
                        {getStatusLabel(order.paid ? order.delivered ? 'completed' : order.assigned ? 'pending' : 'unassigned' : order.assigned ? 'pending' : 'unassigned' )}
                    </Typography>
                </TableCell>
                <TableCell align="right">
                    <Tooltip title="Assign Rider" arrow>
                    <IconButton
                        sx={{
                        '&:hover': { background: theme.colors.warning.lighter },
                        color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        disabled={order.delivered}
                        onClick={event => navigate('/dashboards/orders/assign/' + order.orderId)}
                    >
                        <DeliveryDiningTwoToneIcon fontSize="small" />
                    </IconButton>
                        </Tooltip>
                    <Tooltip title={!order.delivered ? "Set Delivered" : "Set Undelivered"} arrow>
                    <IconButton
                        sx={{
                        '&:hover': { background: theme.colors.info.lighter },
                        color: theme.palette.secondary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={event => modifyDelivered(order.orderId, order.delivered)}
                    >
                        {!order.delivered ? <AllInboxTwoToneIcon fontSize="small" /> : <NotListedLocationTwoToneIcon fontSize="small"/>}
                    </IconButton>
                    </Tooltip>
                    <Tooltip title={!order.paid ? "Set Paid" : "Set Unpaid"} arrow>
                    <IconButton
                        sx={{
                        '&:hover': { background: theme.colors.warning.lighter },
                        color: theme.palette.secondary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={event => modifyPaid(order.orderId, order.paid)}
                    >
                        {!order.paid ? <CreditCardTwoToneIcon fontSize="small" /> : <CreditCardOffTwoToneIcon fontSize="small"/>}
                    </IconButton>
                    </Tooltip>
                </TableCell>
            </TableRow>
            <TableRow>
            <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={4}>
            <Collapse in={open} timeout="auto" unmountOnExit>
                <Box sx={{ margin: 1 }}>
                <Typography variant="h6" gutterBottom component="div">
                    Order Items
                </Typography>
                <Table size="small" aria-label="purchases">
                    <TableHead>
                    <TableRow>
                        <TableCell>Product ID</TableCell>
                        <TableCell>Quantity</TableCell>
                        <TableCell align="right">Total price (₱)</TableCell>
                    </TableRow>
                    </TableHead>
                    <TableBody>
                        {order.contents.map((item, index) => (
                            <TableRow key={0}>
                                <TableCell component="th" scope="row">
                                {item.productId}
                                </TableCell>
                                <TableCell>{item.quantity}</TableCell>
                                <TableCell align="right">
                                    {'₱' + item.price}
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
                <br />
                {Boolean(order.paymentType === "Gcash" && order.paid) && (
                    <Typography variant="body1" gutterBottom component="div">
                        {'*Order was paid through Gcash with Reference ID of: ' + order.paymentProof}
                    </Typography>
                )}
                </Box>
            </Collapse>
            </TableCell>
        </TableRow>
        </>
    )
}

export default OrderRow
