import React, { useState } from 'react';
import {
    Divider,
    Box,
    FormControl,
    InputLabel,
    Card,
    Table,
    TableBody,
    TableCell,
    TableHead,
    TablePagination,
    TableRow,
    TableContainer,
    Select,
    MenuItem,
    CardHeader
  } from '@mui/material';
import OrderRow from './OrderRow';
import { OrderInterface } from 'src/models/orders';

type OrderStatus = 'completed' | 'pending'| 'unassigned';

const statusOptions = [
    {
        id: 'all',
        name: 'All'
    },
    {
        id: 'completed',
        name: 'Completed'
    },
    {
        id: 'pending',
        name: 'Pending'
    },
    {
        id: 'unassigned',
        name: 'Unassigned'
    }
];

const applyPagination = (
    orders: OrderInterface[],
    page: number,
    limit: number
  ): OrderInterface[] => {
    return orders.slice(page * limit, page * limit + limit);
  };

interface Filters {
    status?: OrderStatus;
}

interface TableProps {
    orders: OrderInterface[],
    modifyDelivered: (orderId: string, status: boolean) => void,
    modifyPaid: (orderId: string, status: boolean) => void
}

function OrderTable({ orders, modifyDelivered, modifyPaid }: TableProps) {
    const [paginatedOrders, setPaginatedOrders] = useState<OrderInterface[]>([]);
    const [filteredOrders, setFilteredOrders] = useState<OrderInterface[]>([]);
    const [page, setPage] = useState<number>(0);
    const [limit, setLimit] = useState<number>(5);
    const [filters, setFilters] = useState<Filters>({
        status: null
    });

    const handleStatusChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
        let value = null;
    
        if (e.target.value !== 'all') {
          value = e.target.value;
        }
    
        setFilters((prevFilters) => ({
          ...prevFilters,
          status: value
        }));
    };

    React.useEffect(() => {
        switch (filters.status) {
            case 'pending': 
                let filteredPending: OrderInterface[]= [];
                orders.forEach(order => {
                    if (!order.delivered && order.assigned) {
                        filteredPending.push(order);
                    }
                });
                setFilteredOrders(state => filteredPending);
                break;
            case 'unassigned': 
                let filteredUnassigned: OrderInterface[]= [];
                orders.forEach(order => {
                    if (!order.assigned) {
                        filteredUnassigned.push(order);
                    }
                });
                setFilteredOrders(state => filteredUnassigned);
                break;
            case 'completed': 
                let filtered: OrderInterface[]= [];
                orders.forEach(order => {
                    if (order.delivered && order.paid) {
                        filtered.push(order);
                    }
                });
                setFilteredOrders(state => filtered);
                break;
            default: 
            setFilteredOrders(state => orders);
        }

    }, [orders, filters])

    React.useEffect(() => {
        const pageOrders = applyPagination(filteredOrders, page, limit);
        setPaginatedOrders(state => pageOrders);
    }, [page, limit, filteredOrders])

    const handlePageChange = (event: any, newPage: number): void => {
        setPage(newPage);
    }
    
    const handleLimitChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
        setLimit(parseInt(event.target.value));
    }

    return (
        <Card>
            <CardHeader
                action={
                    <Box width={150}>
                    <FormControl fullWidth variant="outlined">
                        <InputLabel>Status</InputLabel>
                        <Select
                            value={filters.status || 'all'}
                            onChange={handleStatusChange}
                            label="Status"
                            autoWidth
                        >
                            {statusOptions.map((statusOption) => (
                                <MenuItem key={statusOption.id} value={statusOption.id}>
                                {statusOption.name}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    </Box>
                }
                title="Order List"
            />
        
            <Divider />

            <TableContainer>
                <Table>
                    <TableHead>
                    <TableRow>
                        <TableCell />
                        <TableCell>Order ID</TableCell>
                        <TableCell>Customer Name</TableCell>
                        <TableCell>Order Date</TableCell>
                        <TableCell>Amount Due</TableCell>
                        <TableCell>Status</TableCell>
                        <TableCell align="right">Actions</TableCell>
                    </TableRow>
                    </TableHead>
                    <TableBody>
                        {paginatedOrders.map((order, index) => (
                            <OrderRow key={index} 
                                order={order} 
                                modifyDelivered={(orderId, status) => modifyDelivered(orderId, status)}
                                modifyPaid={(orderId, status) => modifyPaid(orderId, status)}
                            />
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
            <Box p={2}>
            <TablePagination
                component="div"
                count={filteredOrders.length}
                onPageChange={handlePageChange}
                onRowsPerPageChange={handleLimitChange}
                page={page}
                rowsPerPage={limit}
                rowsPerPageOptions={[5, 10, 25, 30]}
            />
            </Box>
        </Card>
    )
}

export default OrderTable
