import { DocumentSnapshot } from 'firebase/firestore';

interface OrderItem {
    price: number;
    productId: string;
    quantity: number;
}

interface CustomerInterface {
    contact: string;
    userName: string;
    address?: string;
    email?: string;
    gender?: string;
    latitude?: number;
    longitude?: number;
    uid?: string;
}

export interface OrderInterface {
    orderId: string;
    contents: OrderItem[];
    customer: CustomerInterface;
    amountDue: number;
    assigned: boolean;
    delivered: boolean;
    orderDate: Date | string;
    paid: boolean;
    paymentType: string;
    paymentProof: string;
    latitude?: number;
    longitude?: number;
    userId?: string;
}

class OrderModel {
    orderObject: OrderInterface;

    constructor(order: OrderInterface) {
        this.orderObject = order;
    }

    getOrder(): OrderInterface {
        return this.orderObject;
    }

    setAssigned(value: boolean) {
        this.orderObject.assigned = value;
    }

    setDelivered(value: boolean) {
        this.orderObject.delivered = value;
    }
}

export function convertOrderQuerySnapshots(snapshot: DocumentSnapshot) {
    const orderData = snapshot.data();

    let orderItems: OrderItem[] = []
    orderData.contents.forEach(content => {
        orderItems.push({
            price: content.price,
            productId: content.productId,
            quantity: content.quantity
        })
    });

    const orderQueryData: OrderInterface = {
        orderId: snapshot.id,
        contents: orderItems,
        customer: {
            contact: orderData.customer.contact,
            userName: orderData.customer.userName,
            address: orderData.customer.address,
            email: orderData.customer.email,
            gender: orderData.customer.gender,
            latitude: 0,
            longitude: 0,
            uid: orderData.customer.uid,
        },
        amountDue: orderData.amountDue,
        assigned: orderData.assigned,
        delivered: orderData.delivered,
        orderDate: orderData.orderDate.toDate(),
        paid: orderData.paid,
        paymentType: orderData.paymentType,
        paymentProof: orderData.paymentProof
    }

    return new OrderModel(orderQueryData);
}

export default OrderModel;